import React, { Fragment, useEffect, useState } from "react";
import {
  MdClose,
  MdErrorOutline,
  MdLabel,
  MdSupervisorAccount,
} from "react-icons/md";
import { connect } from "react-redux";
import { StoreState } from "../../reducers";

import { fetchUserAccess } from "../../actions/users";
import { fetchAccessRoles } from "../../actions/access";

import UserList from "./UserList";

import {
  DistrictInterface,
  SystemUserRoleInterface,
  UserInterface,
} from "../../shared/interfaces";
import {
  MainItem,
  SideNavigation,
  SubItem,
  SubItemContainer,
} from "../../components/Navigation/SideNavigation";
import axios from "axios";
import { API } from "../../utils/api";
import { FaFilter } from "react-icons/fa";
import { AiOutlineCaretRight } from "react-icons/ai";
import {
  loadEducationCategories,
  schoolLevelsStoreInterface,
} from "../../actions";
import { IoMdArrowRoundBack } from "react-icons/io";

enum TabTypes {
  All_Users = "All Users",
  Active_Users = "Active Users",
  Blocked_Users = "Blocked Users",
}

interface AppProps {
  userList: UserInterface[];
  accessRoles: SystemUserRoleInterface[];
  schoolLevels: schoolLevelsStoreInterface;
  loadEducationCategories: (callback: (loading: boolean) => void) => void;
  fetchUserAccess: Function;
  fetchAccessRoles: Function;
}

const _UserManagment: React.FC<AppProps> = ({
  userList,
  accessRoles,
  schoolLevels,
  loadEducationCategories,
  fetchUserAccess,
  fetchAccessRoles,
}) => {
  const [labelCounts, setLabelCounts] = useState<{
    allUser: number;
    activeUser: number;
    blockedUser: number;
  }>({
    allUser: 0,
    activeUser: 0,
    blockedUser: 0,
  });
  let userTabs: TabTypes = TabTypes.All_Users;
  // const [userTabs, setUserTabs] = useState<TabTypes>(TabTypes.All_Users);
  const [feedBack, setFeedBack] = useState<{
    type: "success" | "danger" | "warning" | "";
    message: string;
  }>();
  const [district, setDistrict] = useState<DistrictInterface[]>();
  const [userData, setUserData] = useState<UserInterface[]>();
  const [loadingUsers, setLoadingUsers] = useState<boolean>(true);
  const [filterByRoleLevel, setFilerByRoleLevel] = useState<"all" | string>(
    "all"
  );
  const [openFilterByRole, setOpenFilterByRole] = useState<boolean>(false);
  const [loading, setLoading] = useState<boolean>(true);
  const [loadingUser, setLoadingUser] = useState<boolean>(true);
  const [manageUser, setManageUser] = useState<UserInterface | null>(null);
  const [changeRole, setChangeRole] = useState<boolean>(false);
  const [detailFeedBack, setDetailFeedBack] = useState<{
    type: "success" | "danger" | "warning" | "";
    message: string;
  }>({ type: "", message: "" });

  useEffect(() => {
    // LOADING EDUCATION CATEGORY =================================
    if (
      schoolLevels.education_categories === null ||
      schoolLevels.education_categories.length === 0
    ) {
      loadEducationCategories((status: boolean) => {
        console.log({ loadEducationCategories: status });
      });
    }
    // END LOADING EDUCATION CATEGORY =================================

    if (!accessRoles.length) {
      fetchAccessRoles();
    }
    if (loadingUsers && !userList.length) {
      fetchUserAccess();
      setLoadingUsers(false);
    } else {
      setLoadingUsers(false);
    }
    if (!district) {
      loadDistrict();
    }
    if (accessRoles.length && userList.length) {
      console.log({ userList });
      setUserData(userList);
      // REAL CODES TO BE USED ======================
      // setLabelCounts({
      //   allUser: userList.length,
      //   activeUser: userList.filter((user) => user.is_active).length,
      //   blockedUser: userList.filter((user) => !user.is_active).length,
      // });

      // FAKE CODES TO BE USED FOR DISPLAY ======================
      setLabelCounts({
        allUser: userList.length,
        activeUser: userList.filter((user) => !user.is_active).length,
        blockedUser: userList.filter((user) => user.is_active).length,
      });
      setLoading(false);
    }
  }, [
    accessRoles.length,
    district,
    fetchAccessRoles,
    fetchUserAccess,
    loadEducationCategories,
    loadingUsers,
    schoolLevels.education_categories,
    userList,
  ]);

  const handleTabs = (): JSX.Element => {
    if (userTabs === TabTypes.Blocked_Users && district && userData) {
      return (
        <UserList
          users={userData.filter((user) => !user.is_active)}
          roleList={accessRoles}
          userTypes="blocked"
          district={district}
          loadingUser={loadingUser}
          setLoadingUser={setLoadingUser}
          manageUser={manageUser}
          setManageUser={setManageUser}
          changeRole={changeRole}
          setChangeRole={setChangeRole}
          feedBack={detailFeedBack}
          setFeedBack={setDetailFeedBack}
        />
      );
    } else if (userTabs === TabTypes.Active_Users && district && userData) {
      return (
        <UserList
          users={userData.filter((user) => user.is_active)}
          roleList={accessRoles}
          userTypes="active"
          district={district}
          loadingUser={loadingUser}
          setLoadingUser={setLoadingUser}
          manageUser={manageUser}
          setManageUser={setManageUser}
          changeRole={changeRole}
          setChangeRole={setChangeRole}
          feedBack={detailFeedBack}
          setFeedBack={setDetailFeedBack}
        />
      );
    } else if (district && userData) {
      return (
        <UserList
          users={userData}
          roleList={accessRoles}
          userTypes="all"
          district={district}
          loadingUser={loadingUser}
          setLoadingUser={setLoadingUser}
          manageUser={manageUser}
          setManageUser={setManageUser}
          changeRole={changeRole}
          setChangeRole={setChangeRole}
          feedBack={detailFeedBack}
          setFeedBack={setDetailFeedBack}
        />
      );
    } else return <div></div>;
  };

  const filterByRole = (value: string = "all") => {
    let result: UserInterface[];
    if (value === "all") {
      setFilerByRoleLevel("all");
      setUserData(userList);
      setLabelCounts({
        allUser: userList.length,
        activeUser: userList.filter((user) => user.is_active).length,
        blockedUser: userList.filter((user) => !user.is_active).length,
      });
    } else {
      setFilerByRoleLevel(value);
      result = userList.filter(
        (user) =>
          user.default_access &&
          user.default_access.system_user_role_id === value
      );
      setUserData(result);
      setLabelCounts({
        allUser: userList.length,
        activeUser: result.filter((user) => user.is_active).length,
        blockedUser: result.filter((user) => !user.is_active).length,
      });
    }
  };

  const getRoleById = (
    roleList: SystemUserRoleInterface[],
    roleId: string
  ): SystemUserRoleInterface => {
    return roleList.filter((item) => item.system_user_role_id === roleId)[0];
  };

  const countByRole = (value: string): number => {
    if (value === "all") {
      return userList.length;
    }
    return userList.filter(
      (user) =>
        user.default_access && user.default_access.system_user_role_id === value
    ).length;
  };

  const loadDistrict = async () => {
    try {
      const res = await axios.get<DistrictInterface[]>(
        `${API.APPLICATION}/district`
      );
      setDistrict(res.data);
    } catch (error) {
      console.log({ code_error: error });
    }
  };

  if (loading === true) {
    return (
      <div className="text-center mt-7 font-bold text-4xl text-gray-400 animate-pulse">
        Loading users...
      </div>
    );
  }

  return (
    <Fragment>
      {false && (
        <SideNavigation>
          {/* <Search data={[1, 2, 3]} /> */}

          <span className="text-sm mt-2">Filter by role</span>
          <section
            className="mt-2"
            onClick={() => {
              setOpenFilterByRole(!openFilterByRole);
              filterByRole("all");
            }}
          >
            <MainItem
              title={"Users by Role"}
              count={countByRole("all")}
              active={filterByRoleLevel === "all" ? true : false}
            />
          </section>
          {openFilterByRole && filterByRoleLevel && (
            <SubItemContainer>
              {accessRoles.length &&
                accessRoles.map((sub_itm, i: number) => (
                  <section
                    key={sub_itm.system_user_role_id}
                    onClick={() => filterByRole(sub_itm.system_user_role_id)}
                  >
                    <SubItem
                      title={sub_itm.role}
                      icon={MdLabel}
                      active={
                        filterByRoleLevel !== "id" &&
                        sub_itm.system_user_role_id === filterByRoleLevel
                          ? true
                          : false
                      }
                    />
                  </section>
                ))}
            </SubItemContainer>
          )}

          {/* <span className="text-sm mt-2">Filter by Status</span> */}
          {/* <section
          className="mt-2"
          onClick={() => setUserTabs(TabTypes.All_Users)}
        >
          <MainItem
            title={"All Status"}
            count={labelCounts.allUser}
            active={userTabs === TabTypes.All_Users ? true : false}
          />
        </section>
        {
          <SubItemContainer>
            <section onClick={() => setUserTabs(TabTypes.Active_Users)}>
              <SubItem
                title="Active"
                icon={MdLabel}
                active={userTabs === TabTypes.Active_Users ? true : false}
              />
            </section>
            <section onClick={() => setUserTabs(TabTypes.Blocked_Users)}>
              <SubItem
                title="Blocked"
                icon={MdLabel}
                active={userTabs === TabTypes.Blocked_Users ? true : false}
              />
            </section>
          </SubItemContainer>
        } */}
        </SideNavigation>
      )}
      {/* Title */}
      {!manageUser && (
        <div className="w-full my-5 px-1">
          {/* header title */}
          {false && (
            <div className="flex flex-row items-center">
              <MdSupervisorAccount className="text-blue-700 w-36 h-36" />
              <div className="flex flex-col">
                <h1 className="text-3xl font-semibold">
                  System Users Management
                </h1>
                <div className="flex flex-row items-center mx-2 md:gap-3 gap-2">
                  <div className="flex flex-col mt-1">
                    <span className="text-gray-600 text-base">All User(s)</span>
                    <span className="text-gray-600 text-2xl font-bold">
                      {labelCounts.allUser}
                    </span>
                  </div>
                  {userTabs !== TabTypes.Blocked_Users && (
                    <div className="flex flex-col mt-1">
                      <span className="text-gray-600 text-base">
                        Active User(s)
                      </span>
                      <span className="text-gray-600 text-2xl font-bold">
                        {labelCounts.activeUser}
                      </span>
                    </div>
                  )}
                  {userTabs !== TabTypes.Active_Users && (
                    <div className="flex flex-col mt-1">
                      <span className="text-gray-600 text-base">
                        Blocked User(s)
                      </span>
                      <span className="text-gray-600 text-2xl font-bold">
                        {labelCounts.blockedUser}
                      </span>
                    </div>
                  )}
                </div>
              </div>
            </div>
          )}
          <div className="flex flex-row items-center">
            <MdSupervisorAccount className="text-blue-700 w-24 h-24" />
            <div className="flex flex-col">
              <h1 className="text-3xl font-semibold">
                System Users Management
              </h1>
              <div className="flex items-center gap-1">
                <div className="flex flex-row items-center mx-2 md:gap-3 gap-2">
                  <div className="flex items-center mt-1 gap-1">
                    <span className="text-gray-600 text-base italic">
                      All User(s):
                    </span>
                    <span className="text-gray-600 text-2xl font-bold">
                      {labelCounts.allUser}
                    </span>
                  </div>
                </div>
                {filterByRoleLevel !== "all" &&
                  accessRoles.length > 0 &&
                  filterByRoleLevel.length > 0 &&
                  userData && (
                    <div className="flex items-center mt-1">
                      <AiOutlineCaretRight />
                      <span className="text-gray-600 text-base italic mr-1">
                        {getRoleById(accessRoles, filterByRoleLevel).role}
                        {" (s)"}:
                      </span>
                      <span className="text-gray-600 text-2xl font-bold">
                        {userData.length}
                      </span>
                    </div>
                  )}
              </div>
            </div>
          </div>
          {/* end header title */}
        </div>
      )}
      {/* Feedback */}
      {feedBack && feedBack.type !== "" && (
        <div
          className={`alert-${feedBack.type} flex flex-row items-center relative my-2`}
        >
          <span className="absolute top-0 bottom-0 right-0 px-4 py-3">
            <MdClose
              className="h-5 w-5 text-gray-700 hover:font-bold"
              title="Close"
              onClick={() => setFeedBack({ type: "", message: "" })}
            />
          </span>
          <MdErrorOutline className="h-10 w-10" />
          <div className="alert-content ml-4">
            <div className="alert-title font-semibold text-lg">
              {feedBack.type === "success" ? "Success" : "Error"}
            </div>
            <div className="alert-description text-sm">{feedBack.message}</div>
          </div>
        </div>
      )}
      <div
        className={`bg-white shadow rounded border border-gray-2 p-2 w-full overflow-x-auto ${
          manageUser ? "mt-3" : ""
        }`}
      >
        {/* FILTER SECTION ================================ */}
        {!manageUser && (
          <section>
            <div className="my-2 font-medium text-xl flex items-center gap-2">
              <FaFilter />
              Filter by role
            </div>
            <div className="flex items-center md:flex-row flex-col gap-2">
              <div className="flex flex-col mb-3 md:w-1/4 w-full">
                <select
                  className={`w-full bg-white text-grey-darker border-2 border-gray-500 rounded h-10 px-2 focus:outline-none focus:bg-gray-100 animate__animated animate__fadeIn ${
                    loadingUser && "cursor-wait"
                  }`}
                  onChange={(e: React.ChangeEvent<HTMLSelectElement>) =>
                    filterByRole(e.target.value)
                  }
                  disabled={loadingUser}
                >
                  <option value="all">All roles</option>
                  {accessRoles.length > 0 ? (
                    accessRoles.map((item, i) => (
                      <option
                        key={i + item.system_user_role_id}
                        value={item.system_user_role_id}
                      >
                        {item.role}
                      </option>
                    ))
                  ) : (
                    <option value="" disabled>
                      No Role
                    </option>
                  )}
                </select>
              </div>
            </div>
          </section>
        )}
        {manageUser && (
          <div className="flex items-center gap-3 mb-2 text-gray-600">
            <button
              className="inline-block p-3 transition rounded-full bg-gray-400 ripple hover:bg-gray-600 focus:outline-none"
              onClick={() => {
                setManageUser(null);
                setChangeRole(false);
                setDetailFeedBack({ message: "", type: "" });
              }}
            >
              <IoMdArrowRoundBack className="w-6 h-6 text-gray-50" />
            </button>

            <div className="flex flex-col hidden">
              <span className="text-gray-800 font-semibold text-2xl">
                {manageUser.user_info.fname + " " + manageUser.user_info.lname}
              </span>
              <span className="text-gray-600 font-normal text-sm">
                {manageUser.email}
              </span>
            </div>
          </div>
        )}
        {/* END FILTER SECTION ================================ */}
        {handleTabs()}
      </div>
    </Fragment>
  );
};

const mapStateToProps = ({
  users,
  access,
  schoolLevels,
}: StoreState): {
  userList: UserInterface[];
  accessRoles: SystemUserRoleInterface[];
  schoolLevels: schoolLevelsStoreInterface;
} => {
  return { userList: users, accessRoles: access.accessRoles, schoolLevels };
};

export const UserManagment = connect(mapStateToProps, {
  fetchUserAccess,
  fetchAccessRoles,
  loadEducationCategories,
})(_UserManagment);
